
import { computed, defineComponent, onMounted, ref } from 'vue'
export default defineComponent({
  name: 'App',

  setup () {
    // 修正css vh
    const vh = window.innerHeight * 0.01
    document.documentElement.style.setProperty('--vh', `${vh}px`)
    window.addEventListener('resize', () => {
      const vh = window.innerHeight * 0.01
      document.documentElement.style.setProperty('--vh', `${vh}px`)
    })
  }
})
// 修正css vh
